@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Noto Sans KR", sans-serif;
  letter-spacing: -0.02rem;
}

img {
  border: 0;
}
ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
  outline: none;
  color: inherit;
}

input {
  text-decoration: none;
  outline: none;
  border: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input::-webkit-input-placeholder {
  color: #757575;
  font-size: 13px;
}

input:-ms-input-placeholder {
  color: #757575;
  font-size: 13px;
}

input::placeholder {
  color: #757575;
  font-size: 13px;
}

textarea {
  resize: none;
  border: none;
  outline: none;
}

textarea::-webkit-input-placeholder {
  color: #757575;
  font-size: 13px;
}

textarea:-ms-input-placeholder {
  color: #757575;
  font-size: 13px;
}

textarea::placeholder {
  color: #757575;
  font-size: 13px;
}

button {
  background-color: transparent;
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
}

select {
  background: none;
  outline: none;
  border-radius: 0;
  cursor: pointer;
  -moz-appearance: none;
  -webkit-appearance: none;
}

