/* 바로가기 카드 부분 hover css */
.flip-box {
    height: 240px;
    perspective: 1000px;
}
.flip-box:hover .flip {
    transform: rotateY(-180deg);
}
.flip {
    width: 100%;
    height: 100%;
    position: relative;
    color: white;
    transform-style: preserve-3d;
    transform: rotateY(0deg);
    transition: .5s;
}
.front1, .back1 ,.front2 , .back2, .front3, .back3 {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    
}
.front1 {
    background-image: linear-gradient( 135deg, #ABDCFF 10%, #0396FF 100%);
}
.back1 {
    background-image: linear-gradient( 135deg,  #0396FF 10%,#ABDCFF 100%);
    transform: rotateY(180deg);
}
.front2 {
    background-image: linear-gradient( 135deg, #90F7EC 10%, #32CCBC 100%);
}
.back2 {
    background-image: linear-gradient( 135deg, #32CCBC 30%,#90F7EC 60%);
    transform: rotateY(180deg);
}
.front3 {
    background-image: linear-gradient( 135deg, #65FDF0 10%, #1D6FA3 100%);
}
.back3 {
    background-image: linear-gradient( 135deg, #65FDF0 20%, #1D6FA3 100%);
    transform: rotateY(180deg);
}

